.container__advantages {
  @media screen and (min-width: 1280px) {
    margin-top: -25px;
  }
}
.advantages {
  @media screen and (min-width: 768px) {
    display: flex;
  }
}
.advantages__item {
  @media screen and (max-width: 767px) {
    &:not(:first-child) {
      margin-top: 38px;
    }
  }

  @media screen and (min-width: 768px) {
    width: calc((100% - 2 * 60px) / 3);

    &:not(:first-child) {
      margin-left: 60px;
    }
  }
  @media screen and (min-width: 1280px) {
    width: calc((100% - 2 * 30px) / 3);

    &:not(:first-child) {
      margin-left: 30px;
    }
  }
}
.advantages__item:before {
  content: '';
  display: block;
  width: 50px;
  height: 50px;
  background-repeat: no-repeat;
  background-size: contain;
  background-image: url('../images/Advantages/advantages-icon1.png');
}
@media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
  .advantages__item:before {
    background-image: url('../images/Advantages/advantages-icon1@2x.png');
  }
}

.advantages__index {
  font-family: Titan One;
  font-size: 36px;
  line-height: 1.14;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  display: block;

  color: var(--accent-color);
  margin-top: 16px;
  margin-bottom: 16px;

  @media screen and (min-width: 768px) {
    font-size: 48px;
    margin-top: 36px;
    margin-bottom: 15px;
  }
  @media screen and (min-width: 1280px) {
    font-size: 58px;
    margin-top: 40px;
    margin-bottom: 15px;
  }
}
.advantages__text {
  font-size: 12px;
  font-weight: 700;
  font-size: 12px;
  line-height: 1.74;
  max-width: 230px;

  @media screen and (min-width: 768px) {
    font-size: 14px;
    max-width: 180px;
  }
  @media screen and (min-width: 1280px) {
    font-size: 16px;
    max-width: 330px;
  }
}
