:root {
  --brand-color: #ffb8ca;
  --light-brand-color: #fff5f6;
  --dark-brand-color: #ffa5ba;
  --accent-color: #d41443;

  --main-bgc: #fff;
  --light-section-bgc: #fff5f6;
  --dark-section-bgc: #e5e5e5;

  --main-text-color: #000;
  --secondary-text-color: #907e82;

  --products-second-card-color: #f0d1a5;
  --products-third-card-color: #c2e297;

  --light-button-bgc: #ecebeb;
  --hover-button-bgc: #c7133e;

  --mobile-menu-bgc: #e17992;

  --slider-item-color: #d9d9d9;

  --contacts-icon-bgc: #fea5bb;

  --button-border-radius: 22px;
  --card-border-radius: 24px;
}

